import React, { Component } from 'react';
import IndexFriendLink from './IndexFriendLink'
import IndexCont from './IndexCont'

class Index extends Component{
	constructor(props){
		super(props)
	}
	render(){
		return (
			<div>
				<IndexCont></IndexCont>
				<IndexFriendLink></IndexFriendLink>
			</div>
		)
	}
}

export default Index;


